import * as React from 'react';
import { DisplayMode } from '@microsoft/sp-client-base';

import './ZoneHost.scss';

export interface IZoneHostProps {
  /* tslint:disable:no-any */
  addinType: new (element: HTMLElement) => any;
  /* tslint:enable:no-any */
  isEditing?: boolean;
}

export interface IZoneHostState {
  isDropping: boolean;
}

export default class ZoneHost extends React.Component<IZoneHostProps, IZoneHostState> {
  public static columnDefinition: string = 'ms-u-sm12';

  public refs: {
    [key: string]: React.ReactInstance;
    zoneArea: HTMLDivElement;
  };

  public constructor() {
    super();

    this.state = {
      isDropping: false
    };
  }

  public render(): React.ReactElement<IZoneHostProps> {
    return (
      <div
        className={ this._getClassName() }
        ref='zoneArea'
        onDragOver={ this._onDragOver.bind(this) }
        onDragLeave={ this._onDragLeave.bind(this) }>

        I am a webpart.
        </div>
    );
  }

  public componentDidMount(): void {
    /* tslint:disable:no-any */
    const addinType: new (element: HTMLElement) => any = this.props.addinType;
    /* tslint:enable:no-any */

    if (addinType) {
      const zoneAreaElement: HTMLElement = this.refs.zoneArea as HTMLElement;
      /* tslint:disable:no-any */
      const addin: any = new addinType(zoneAreaElement);
      /* tslint:enable:no-any */

      addin.render(DisplayMode.Read);
    }
  }

  private _getClassName(): string {
    return 'ZoneHost' +
           (this.props.isEditing ? ' is-editing' : '') +
           (this.state.isDropping ? ' ZoneHost--isDropping' : '');
  }

  private _onDragOver(ev: React.DragEvent): void {
    ev.preventDefault();
    this.setState({
      isDropping: true
    });
  }

  private _onDragLeave(ev: React.DragEvent): void {
    this.setState({
      isDropping: false
    });
  }

}




