import { observable } from 'mobservable';

import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';

export interface IWebPartStore {
  /* tslint:disable:no-any */
  parts: { [key: string]: IClientSideWebPartManifest<any> };
  /* tslint:enable:no-any */
}

export default class AddinsPickerStore {
  public searchLabel: string;
  public searchText: string;
  public errorMessage: string;

  @observable public filterText: string;
  @observable public isLoading: boolean;

  private _partStore: IWebPartStore;

  constructor(webPartStore: IWebPartStore) {
    this.searchLabel = 'Search';
    this.searchText = '';
    this.isLoading = false;
    this.errorMessage = undefined;
    this.filterText = undefined;
    this._partStore = webPartStore;
  }

  /* tslint:disable:no-any */
  @observable public get parts(): IClientSideWebPartManifest<any>[] {
    const filteredParts: IClientSideWebPartManifest<any>[] = [];
  /* tslint:enable:no-any */
    const filterText: string = (this.filterText || '').toLowerCase();

    for (const partId in this._partStore.parts) {
      if (this._partStore.parts[partId]) {
        /* tslint:disable:no-any */
        const partManifest: IClientSideWebPartManifest<any> = this._partStore.parts[partId];
        /* tslint:enable:no-any */

        if (!filterText || partManifest.preconfiguredEntries[0].title.default.toLowerCase().indexOf(filterText) > -1) {
          filteredParts.push(partManifest);
        }
      }
    }

    return filteredParts.sort((a, b) => {
      return (a && a.preconfiguredEntries[0].title.default &&
                   a.preconfiguredEntries[0].title.default.localeCompare(b.preconfiguredEntries[0].title.default));
    });
  }

  public filter(filterText: string): void {
    this.filterText = filterText;
  }
}



