import * as React from 'react';
import './CommandBar.scss';
import { IPageStore } from '../page/PageStore';
import { IWorkBenchStore } from '../../stores/WorkBenchStore';
import strings from './CommandBar.resx';

export interface IPageCommandBarProps {
  store: IPageStore;
  appStore: IWorkBenchStore;
  onClickMobileView: () => void;
  onClickTabletView: () => void;
  closeMobilePreview: () => void;
}

export default class CommandBar extends React.Component<IPageCommandBarProps, void> {

  private _pageStore: IPageStore;

  constructor(props: IPageCommandBarProps) {
    super(props);

    this._pageStore = props.store;
    this._onClickClear = this._onClickClear.bind(this);
    this._onClickReadEdit = this._onClickReadEdit.bind(this);
  }

  public render(): React.ReactElement<IPageCommandBarProps> {
    if (this.props.appStore.isEditing) {
      return (
        <div className='ms-CommandBar'>
          <div className='ms-CommandBar-sideCommands'>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link' onClick={ this.props.onClickMobileView }>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--mobile'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>
                    { strings.Mobile }</span>
                </a>
              </div>
            </div>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link' onClick={ this.props.onClickTabletView }>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--tablet'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>
                    { strings.Tablet }</span>
                </a>
              </div>
            </div>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link' onClick={ this._onClickReadEdit.bind(this) }>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--eye'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>Preview</span>
                </a>
              </div>
            </div>
          </div>
          <div className='ms-CommandBar-mainArea'>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link'>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--save'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>Save</span>
                </a>
              </div>
            </div>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link' onClick={ this._onClickClear.bind(this) }>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--reactivate'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>Discard</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className='ms-CommandBar'>
          <div className='ms-CommandBar-sideCommands'>
            <div className='ms-CommandBarItem'>
              <div className='ms-CommandBarItem-linkWrapper'>
                <a className='ms-CommandBarItem-link' onClick={ this._onClickReadEdit.bind(this) }>
                  <span className='ms-CommandBarItem-icon ms-Icon ms-Icon--pencil'></span>
                  <span className='ms-CommandBarItem-commandText ms-font-m ms-font-weight-regular'>Edit</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }

  private _onClickClear(): void {
    this._pageStore.clearPageState();
  }

  private _onClickReadEdit(): void {
    this.props.appStore.toggleEdit();
  }

}



