import { observable, transaction, asReference } from 'mobservable';
import 'whatwg-fetch';

export default class WebPartStore {
  @observable public errorMessage: string;
  @observable public isLoading: boolean;
  /* tslint:disable:no-any */
  @observable public parts: { [key: string]: any};
  /* tslint:enable:no-any */

  constructor() {
    this.isLoading = false;
    this.errorMessage = undefined;
    this.parts = asReference({});

    // @todo: removing this as this is not required right now. Will cleanup this with other workbench cleanup
    // at a later date.
    // this.fetchWebParts();
  }

  public fetchWebParts(): void {
    if (!this.isLoading) {
      this.isLoading = true;

      /* tslint:disable:no-any */
      if ((window as any).webpartManifests) {
        this.parts = (window as any).webpartManifests;
      /* tslint:enable:no-any */
        this.isLoading = false;
      } else {
        // Get the webparts!
        // @todo: remove hardcoded url, add error handling, where is cancel...
        window.fetch('http://localhost:5432/getwebparts')
          .then((response: Response) => response.json())
          /* tslint:disable:no-any */
          .then((json: any) => {
          /* tslint:enable:no-any */
            transaction(() => {
              this.parts = json;
              this.isLoading = false;
            });
          })
          .catch((ex: Error) => {
            console.error(ex);
            this.errorMessage = ex.message;
            this.isLoading = false;
          });
      }
    }
  }
}



