import { observable } from 'mobservable';
import { ServiceScope, IServiceScopeProvider } from '@microsoft/sp-client-base';
import * as lodash from '@microsoft/sp-lodash-subset';

export interface IPageDefinition {
  officeHeaderTitle: string;
  hasOfficeHeader: boolean;
  hasCommandBar: boolean;
  hasNavPane: boolean;
  hasDetailsPane: boolean;
}

export interface IPageStore extends IServiceScopeProvider {
  pageDefinition: IPageDefinition;
  setSerializeCallback: (callback: () => string) => void;
  logSerializedStateCallback: (callback: (b: boolean) => string) => void;
  setClearCallback: (callback: () => void) => void;
  getPageState: () => string;
  clearPageState: () => void;
  getPageReadyPromise(): Promise<void>;
}

export class PageStore implements IPageStore {
  public _clearPage: () =>  void;
  @observable public pageDefinition: IPageDefinition;

  private _serviceScope: ServiceScope;
  private _PAGE_DATA_KEY: string = 'sp_webpart_workbench_state';
  private _autoSave: boolean;
  private _serializePage: () => string;
  private _resolvePageReady: () => void;
  private _pageReadyPromise: Promise<void> = new Promise<void>((resolve: () => void) => {
    this._resolvePageReady = resolve;
  });

  constructor(serviceScope: ServiceScope) {
    this._serviceScope = serviceScope;

    this.pageDefinition = {
      officeHeaderTitle: 'Office 365',
      hasOfficeHeader: false,
      hasNavPane: false,
      hasCommandBar: false,
      hasDetailsPane: true
    };

    this._autoSave = true;

    // @todo: temporary solution to unblock Mike Wheaton. Will change this to depend on Canvas dirty state.
    if (this._autoSave) {
      window.setInterval(this.savePageState.bind(this), 3 * 1000);
    }
  }

  public get serviceScope(): ServiceScope {
    return this._serviceScope;
  }

  public setSerializeCallback(callback: () => string): void {
    this._serializePage = callback;
  }

  public logSerializedStateCallback(callback: (b: boolean) => string): void {
    /* tslint:disable:no-string-literal */
    if (window['SP']) {
      window['SP'].logSerializedPageState = callback;
    /* tslint:enable:no-string-literal */
    }
  }

  public setClearCallback(callback: () => void): void {
    this._clearPage = callback;
  }

  public update(options: IPageDefinition): void {
    lodash.merge(this.pageDefinition, options);
  }

  public savePageState(): void {
    if (this._autoSave && this._serializePage) {
      sessionStorage.setItem(this._PAGE_DATA_KEY, this._serializePage());
    }
  }

  public clearPageState(): void {
    if (this._autoSave) {
      sessionStorage.removeItem(this._PAGE_DATA_KEY);
    }

    this._clearPage();
  }

  public getPageState(): string {
    if (this._autoSave) {
      return sessionStorage.getItem(this._PAGE_DATA_KEY);
    } else {
      return undefined;
    }
  }

  public getPageReadyPromise(): Promise<void> {
    return this._pageReadyPromise;
  }

  public setPageReady(): void {
    this._resolvePageReady();
  }
}



