import * as React from 'react';

import ZoneHost from '../zoneHost/ZoneHost';
import CommandBar from '../commandBar/CommandBar';
import DetailsPane from '../detailsPane/DetailsPane';
import Page from '../page/Page';
import { observer } from 'mobservable-react';
import { PageStore } from '../page/PageStore';
import WebPartStore from '../../stores/WebPartStore';
import { IWorkBenchStore } from './../../stores/WorkBenchStore';
import MobilePreview, { IMobilePreviewProps, DeviceType } from '../mobilePreview/mobilePreview/MobilePreview';

import './WorkBench.scss';

export interface IWorkBenchProps {
  store: IWorkBenchStore;
  pageStore: PageStore;
}

export interface IWorkBenchState {
  displayMobilePreview: boolean;
  mobilePreviewDeviceType: DeviceType;
}

export interface IWorkBenchContext {
  pageStore: PageStore;
  webPartStore: WebPartStore;
}

@observer export class WorkBench extends React.Component<IWorkBenchProps, IWorkBenchState> {
  /* tslint:disable:no-any */
  public static childContextTypes: any = {
  /* tslint:enable:no-any */
    pageStore: React.PropTypes.object,
    webPartStore: React.PropTypes.object
  };

  private _webPartStore: WebPartStore;

  constructor() {
    super();

    this._webPartStore = new WebPartStore();

    this.state = {
      displayMobilePreview: false,
      mobilePreviewDeviceType: DeviceType.Phone
    };

    this._onClickMobileView = this._onClickMobileView.bind(this);
    this._onClickTabletView = this._onClickTabletView.bind(this);
    this._closeMobilePreview = this._closeMobilePreview.bind(this);
    this._setSuiteNavZIndex = this._setSuiteNavZIndex.bind(this);
  }

  // React magically finds this function by looking for the specific function name.
  // https://facebook.github.io/react/docs/context.html
  public getChildContext(): IWorkBenchContext {
    return {
      pageStore: this.props.pageStore,
      webPartStore: this._webPartStore
    };
  }

  public render(): React.ReactElement<IWorkBenchProps> {
    const zones: React.ReactElement<{}>[] = [];
    let mobilePreview: React.ReactElement<IMobilePreviewProps> = undefined;
    const { addinType, isEditing, isDetailsPaneVisible }: IWorkBenchStore = this.props.store;
    const rootClass: string = 'WorkBench ms-font-m' +
      (isEditing ? ' is-editing' : '') +
      (isDetailsPaneVisible ? ' has-pane' : '');

    const pageStore: PageStore = this.props.pageStore;

    if (addinType) {
      zones.push(<ZoneHost addinType={ addinType } />);
    }

    if (this.state.displayMobilePreview) {
      mobilePreview = <MobilePreview
                      onExit = { this._closeMobilePreview }
                      deviceType = { this.state.mobilePreviewDeviceType } />;
    }

    return (
      <div className={ rootClass }>
        <div className='WorkBench-header'>
          <div className='WorkBench-headerMenu'>
            <i className='ms-Icon ms-Icon--waffle'></i>
          </div>
          <div className='WorkBench-headerTitle ms-font-xl'><em>Office 365</em> SharePoint Workbench</div>
          <div className='WorkBench-headerPerson'>
            <i className='ms-Icon ms-Icon--person'></i>
          </div>
        </div>
        <div>
          <div id='workbenchCommandBar' className='WorkBench-commandBar'>
            <CommandBar store={ pageStore } appStore={ this.props.store } onClickMobileView={ this._onClickMobileView }
            onClickTabletView={ this._onClickTabletView } closeMobilePreview={ this._closeMobilePreview }/>
          </div>
          <div id='workbenchPageContent' className='WorkBench-pageContent'>
            <Page store={ pageStore } appStore={ this.props.store } />

            {/**
              * @todo: DetailsPane is currently mandatory. The reason being that the WebPartStore makes the getWebPart
              * API call and then updates the AddinPickerStore which in turn updaets the Toolbox Items. As we evolve the
              * Workbench functionality and design, we need to design that correctly. But for now, we have a hidden
              * DetailsPane component as a temporary hack.
              */}
            <DetailsPane />
          </div>
        </div>
        { zones }
        { mobilePreview }
      </div>
    );
  }

  private _onClickMobileView(): void {
    this.setState({
      displayMobilePreview: true,
      mobilePreviewDeviceType: DeviceType.Phone
    });
    this._setSuiteNavZIndex('0');
  }

  private _onClickTabletView(): void {
    this.setState({
      displayMobilePreview: true,
      mobilePreviewDeviceType: DeviceType.Tablet
    });
    this._setSuiteNavZIndex('0');
  }

  private _closeMobilePreview(): void {
    this.setState({
      displayMobilePreview: false,
      mobilePreviewDeviceType: DeviceType.Phone
    });
    this._setSuiteNavZIndex('5');
  }

  /**
   * Sets the z-index of the suite nav.
   * Purpose of this is so that the suite nav doesn't cover up the mobile preview header.
   * Should find a better way to achieve this.
   */
  private _setSuiteNavZIndex(value: string): void {
    const suiteNav: HTMLElement = document.getElementById('SuiteNavPlaceHolder');
    if (suiteNav) {
      suiteNav.style.zIndex = value;
    }
  }
}



