import * as React from 'react';
import {
  TextField,
  Button
} from 'office-ui-fabric-react';
import styles from './MobilePreviewURLEntry.module.scss';

export interface IMobilePreviewURLEntryProps {
  onChangeURL: (value: string) => void;
  onClickURLSubmit: () => void;
}

export default class MobilePreviewURLEntry extends React.Component<IMobilePreviewURLEntryProps, void> {
  constructor(props: IMobilePreviewURLEntryProps) {
    super(props);

    this._validateAndCompleteURL = this._validateAndCompleteURL.bind(this);
  }

  public render(): React.ReactElement<{}> {
    return(
    <div className={ styles.mobilePreviewURLbar }>
      <TextField className={ styles.mobilePreviewTextfield }
          placeholder='Enter a URL to view in Mobile Preview' onChanged={ this._validateAndCompleteURL }/>
      <Button className={ styles.mobilePreviewURLbutton } onClick={ this.props.onClickURLSubmit }>View Preview</Button>
    </div>
    );
  }

  /**
   * If the URL is not valid, an error page displays as it would in the browser. As such, no additional validation is
   * conducted here aside from not allowing a blank entry.
   */
  private _validateAndCompleteURL(value: string): void {
    if (!value || value === '') {
      return;
    }
    const regex: RegExp = /https?:\/\//;
    if (!regex.test(value)) {
      value = 'http://' + value;
    }
    this.props.onChangeURL(value);
  }
}


