import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewRotate.module.scss';

import { DeviceOrientation } from '../mobilePreview/MobilePreview';

export interface IMobilePreviewRotateProps {
  onRotate: (orientation: DeviceOrientation) => void;
  currentOrientation: DeviceOrientation;
}

export default class MobilePreviewRotate extends React.Component<IMobilePreviewRotateProps, void> {
  constructor(props: IMobilePreviewRotateProps) {
    super(props);

    this._onClickRotate = this._onClickRotate.bind(this);
  }

  public render(): React.ReactElement<{}> {
    const rotateIconClass: string = css('ms-Icon', 'ms-Icon--reload', 'ms-fontColor-neutralLight',
      styles.mobilePreviewRotateIcon);

    return(
      <div>
        <a onClick={ this._onClickRotate }>
          <span className={ rotateIconClass }></span>
        </a>
      </div>
    );
  }

  private _onClickRotate(): void {
    const orientation: DeviceOrientation = this.props.currentOrientation === DeviceOrientation.Portrait ?
        DeviceOrientation.Landscape : DeviceOrientation.Portrait;

    this.props.onRotate(orientation);
  }
}


