import * as React from 'react';
import SearchBox from '../searchBox/SearchBox';
import './AddinPickerPane.scss';
import { observer } from 'mobservable-react';

export interface IAddinPickerPaneProps {
  store?: {
    filterText: string;
    isLoading?: boolean;
    errorMessage?: string;
    /* tslint:disable:no-any */
    parts: any[];
    /* tslint:enable:no-any */

    filter: (filterText?: string) => void;
  };
}

@observer
export default class AddinPickerPane extends React.Component<IAddinPickerPaneProps, void> {
  constructor() {
    super();
  }

  public render(): React.ReactElement<IAddinPickerPaneProps> {
    const { store }: IAddinPickerPaneProps = this.props;

    const itemElements: React.ReactElement<{}>[] = store.parts ?
    store.parts.map(addin => (
      <div className='List-cell' key={ addin.id }>
        <div className='Item' draggable={ true }>
          <div className={'Item-image ms-Icon ms-Icon--' + addin.iconFontName}></div>
          <div className='Item-title ms-font-l'>{ addin.displayName }</div>
          <div className='Item-description ms-font-s'>{ addin.description }</div>
        </div>
      </div>
    ))
    /* tslint:disable:no-null-keyword */
    : null;
    /* tslint:enable:no-null-keyword */

    return (
      <div className='AddinPickerPane'>
        <SearchBox label='Search' onChange={ this._onFilter.bind(this) }/>

        { !store.isLoading && store.filterText && !store.parts.length ?
        <div className='AddinPickerPane-message'>No results</div>
        :
        <div className='List'>
          { store.isLoading ?
          <div className='AddinPickerPane-message'>Loading...</div>
          : itemElements }

          { store.errorMessage
            ? <div className='AddinPickerPane-error'>{ store.errorMessage }</div>
            /* tslint:disable:no-null-keyword */
            : null }
            /* tslint:enable:no-null-keyword */
        </div>
        }
      </div>
    );
  }

  private _onFilter(filterText: string): void {
    this.props.store.filter(filterText);
  }
}



