import * as React from 'react';

import { observer } from 'mobservable-react';
import NavPane from '../navPane/NavPane';
import { IPageStore, IPageDefinition } from './PageStore';
import { Canvas } from '@microsoft/sp-client-preview';
import { IWorkBenchStore } from '../../stores/WorkBenchStore';
/* tslint:disable:max-line-length */
import styles from '@microsoft/sp-client-preview/lib/local/clientSideWebParts/propertyPane/propertyPaneController/PropertyPaneController.module.scss';
/* tslint:enable:max-line-length */

import './Page.scss';

export interface IPageProps {
  store: IPageStore;
  appStore: IWorkBenchStore;
}

@observer
export default class Page extends React.Component<IPageProps, void> {
  private _canvas: Canvas;

  constructor() {
    super();
  }

  public componentDidMount(): void {
    this._canvas = new Canvas(
      // @todo (SPPPlat VSO bug #189028) This is a workaround for a TypeScript compiler bug
      // that we reported.  After the compiler is fixed, we will remove the typecast.
      /* tslint:disable:no-any */
      this.props.store.serviceScope as any,
      /* tslint:disable:no-any */
      document.querySelector('#Canvas') as HTMLDivElement,
      this.props.appStore.displayMode
    );

    // Don't render webparts until the page is ready
    this.props.store.getPageReadyPromise().then(() => {
      this._canvas.render(this.props.store.getPageState());

      // @todo: the canvas has a bug and needs any empty array in order to render. Ideally render() should
      // render an empty canvas.
      this.props.store.setClearCallback(this._clearPage.bind(this));
      this.props.store.setSerializeCallback(() => { return this._canvas.serialize(); });
      this.props.store.logSerializedStateCallback((): string => {
        return this._canvas.serialize();
      });
    });
  }

  public componentWillReceiveProps(props: IPageProps): void {
    if (this._canvas) {
      this._canvas.displayMode = this.props.appStore.displayMode;
    }
  }

  public render(): React.ReactElement<IPageProps> {
    const {
      hasOfficeHeader,
      hasNavPane,
      hasCommandBar,
      hasDetailsPane
    }: IPageDefinition = this.props.store.pageDefinition;

    const rootClass: string = _getClassName(
      'Page',
      {
        'Page--hasHeader': hasOfficeHeader,
        'Page--hasNavPane': hasNavPane,
        'Page--hasCommandBar': hasCommandBar,
        'Page--hasDetailsPane': hasDetailsPane
      });

    return (
      <div className={ rootClass }>

        <div className='Page-belowHeader'>

          { hasNavPane
            ? <NavPane/>
            /* tslint:disable:no-null-keyword */
            : null
            /* tslint:enable:no-null-keyword */
          }

          <div id='pageContent' className='Page-content'>
            <div id='Canvas'/>
          </div>
        </div>
      </div>
    );
  }

  private _clearPage(): void {
    this._canvas.render();

    // Removing the property pane with slide effect, when discard is clicked.
    const propertyPaneContainer: HTMLDivElement =
      document.getElementById('propertyPaneContainer') as HTMLDivElement;
    if (propertyPaneContainer) {
      propertyPaneContainer.classList.remove(styles.showPane);

      // This is to see the slide effect of the property pane and then empty the container.
      window.setTimeout(() => {
        while (propertyPaneContainer.firstChild) {
          propertyPaneContainer.removeChild(propertyPaneContainer.firstChild);
        }
      }, 1000);
    }
  }
}

function _getClassName(baseClass: string, map: { [key: string]: boolean }): string {
  let className: string = baseClass;

  for (const flag in map) {
    if (map.hasOwnProperty(flag) && map[flag]) {
      className += ' ' + flag;
    }
  }

  return className;
}



