import * as React from 'react';

import AddinPickerPane from '../addinPickerPane/ConnectedAddinPickerPane';

import './DetailsPane.scss';

/* tslint:disable:export-name */
export default class WorkBench extends React.Component<void, void> {
/* tslint:enable:export-name */
  public render(): React.ReactElement<{}> {
    return (
      <div className='DetailsPane ms-font-m'>

        <div className='DetailsPane-header'>
          <i className='DetailsPane-closeButton ms-Icon ms-Icon--x'></i>
          <div className='DetailsPane-headerText'>WebParts</div>
        </div>

        <div className='DetailsPane-content'>

          <div className='DetailsPane-section'>
            <div className='DetailsPane-sectionContent'>
              <AddinPickerPane />
            </div>
          </div>

        </div>
      </div>
    );

  }
}



