import * as React from 'react';

import SearchBox from '../searchBox/SearchBox';

import './NavPane.scss';

export default class NavPane extends React.Component<void, void> {
  public render(): React.ReactElement<{}> {
    return (
      <div className='NavPane ms-font-m'>
        <div className='NavPane-search'>
          <SearchBox label='Search' />
        </div>
        <div className='NavPane-link'>Home</div>
        <hr />

        <div className='NavPane-link'>Link 1</div>
        <div className='NavPane-link'>Link 2</div>
        <div className='NavPane-link'>Link 3</div>
      </div>
    );

  }
}



