require('set-webpack-public-path!');

/* tslint:disable:no-unused-variable */
import * as React from 'react';
/* tslint:enable:no-unused-variable */
import * as ReactDOM from 'react-dom';
import { ClientSideApplication } from '@microsoft/sp-client-preview';
import moduleLoader from '@microsoft/sp-module-loader';

import { WorkBench } from './components/Workbench/Workbench';

import { WorkBenchStore } from './stores/WorkBenchStore';
import { PageStore } from './components/page/PageStore';

import './workbench.module.scss';

class SpWebpartWorkbench extends ClientSideApplication {
  private _pageStore: PageStore;

  protected onLoad(): void {
    // @todo (SPPPlat VSO bug #189028) This is a workaround for a TypeScript compiler bug
    // that we reported.  After the compiler is fixed, we will remove the typecast.
    /* tslint:disable:no-any */
    this._pageStore = new PageStore(this.shell.serviceScope as any);
    /* tslint:enable:no-any */

    // Only attempt to load debug manifests if we're running on a SharePoint box
    if (!window.location.hostname.match(/localhost/i)) {
      moduleLoader.forceLoadDebugManifests().then(() => {
        /* tslint:disable:no-console */
        console.debug('Debug manifests loaded');
        /* tslint:enable:no-console */

        this._pageStore.setPageReady();
      });
    } else {
      this._pageStore.setPageReady();
    }
  }

  protected onRender(): void {
    const workbenchstore: WorkBenchStore = new WorkBenchStore();

    ReactDOM.render(<WorkBench store={ workbenchstore } pageStore={ this._pageStore } />, this.domElement);
  }
}

/* tslint:disable:export-name */
export default new SpWebpartWorkbench() as ClientSideApplication;
/* tslint:enable:export-name */



